<template>
    <div class="login">
        <div id="login-panel">
            <el-card shadow="always" id="login-card" v-loading="loading">
                <el-form ref="form" :model="form" :rules="rules">
                    <el-form-item><h2>JEDDAK 协同计算平台</h2></el-form-item>
                    <el-form-item prop="name">
                        <el-input
                            placeholder="请输入用户名"
                            v-model="form.name" >
                        </el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input
                            placeholder="请输入密码"
                            v-model="form.password"
                            v-on:keyup.enter.native="onSubmit"
                            show-password >
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" v-on:click="onSubmit" id="btn-submit">登录</el-button>
                    </el-form-item>
                </el-form>
            </el-card>
        </div>
    </div>
</template>

<script>
    import { sha256 } from 'js-sha256'
    export default {
        name: 'Login',
        data: function () {
            return {
                form: {
                    name: null,
                    password: null,
                },
                rules: {
                    name: [
                        { required: true, trigger: 'blur' }
                    ],
                    password: [
                        { required: true, trigger: 'blur' }
                    ],
                },
                loading: false,
                salt: '',
                password:''
            }
        },
        methods: {
            // 提交登录信息
            async onSubmit() {
                this.loading = true;
                // get salt
                try{
                  const { data: resp } = await this.$axios.post("/board/user", {
                    'filter': {name: this.form.name},
                    'req_type': 'get_salt'
                  });
                  if(resp.data.salt !== ""){
                    this.salt = resp.data.salt
                  }
                }catch (error) {
                  if (error.response) {
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                    this.$notify.error({
                      title: '提示',
                      message: '获取盐值失败'
                    });
                    this.loading = false;
                  }
                  console.log(error);
                }
                this.password = sha256(this.salt + this.form.password).toString();
                // send login request
                try {
                  const { data: res } = await this.$axios.post("/board/user", {
                    'filter': {name: this.form.name, password: this.password},
                    'req_type': 'login'
                  })
                  if(res.status === 200){
                    this.loading = false;

                    this.$notify.success({
                      title: '提示',
                      message: '登录成功'
                    });
                    window.localStorage.setItem("jeddak_token", res.data.token);
                    window.localStorage.setItem("jeddak_username", this.form.name)
                    // console.log(res.data)
                    this.$global.isAdmin = res.data.isAdmin === true;
                    window.localStorage.setItem("jeddak_isAdmin",JSON.stringify(this.$global.isAdmin));
                    await this.$router.push("/");
                    this.loading = false;
                  }
                } catch (error) {
                  if (error.response) {
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                    this.$notify.error({
                      title: '提示',
                      message: '用户名或密码错误'
                    });
                    this.loading = false;
                  }
                  console.log(error);
                }
            }
        }
    }
</script>

<style scoped>
    #login-panel {
        display: flex;
        justify-content: center;
        margin-top: 150px;
    }
    #login-card {
        width: 500px;
        background-color: #eeeeee;
        padding-bottom: 30px;
    }
    #btn-submit {
        width: 100%
    }
</style>
